@font-face {
  font-family: "SFProText";
  src: local("SFProText"),url("./assets/fonts/SF-Pro-Text-Regular.otf") format("opentype");
}
@font-face {
  font-family: "SFProDisplay";
  src: local("SFProDisplay"),url("./assets/fonts/SF-Pro-Display-Regular.otf") format("opentype");
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','SFProText','SFProDisplay',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
