$light: #f8f8f8;
$tertiary: #327fbf;
$dimGray: #747474;
$red: #bf3a32;
$white: #ffffff;
$sfProText: "SFProText";
$nero: #212121;
$dark: #4b4b4b;
$gray: #d6d6d6;
$secondary: #199fc8;
$gray92: #ebebeb;
$lightBlack: #3e3a37;
$kilimanjaro: #3a3532;
$fontSize15: 15px;
$fontSize18: 18px;

.textInput {
  width: 422px;
  height: 55px;
  background-color: $light;
  border-radius: 8px;
  border-style: none;
}
.textInput:focus {
  outline: none;
  border-bottom: 2px solid $dimGray;
}
.textInput:invalid {
  border-bottom: 1px solid $red;
}
.btn-small {
  &.MuiButton-root {
    max-width: 40px;
    min-width: 40px;
    max-height: 40px;
    min-height: 40px;
    border-radius: 8px;
  }
}
.btn-large {
  width: 422px;
  height: 55px;
  &.MuiButtonBase-root:hover {
    background-color: $tertiary;
  }
  &.MuiButtonBase-root {
    background-color: $tertiary;
    border-radius: 8px;
    font-weight: 500;
    font-size: 15px;
    color: $white;
    font-family: $sfProText;
    font-style: normal;
  }
}
.btn-cancel {
  width: 422px;
  height: 55px;
  &.MuiButtonBase-root {
    border: 1px solid $nero;
    border-radius: 8px;
    font-weight: 500;
    font-size: 15px;
    color: $nero;
    font-family: $sfProText;
    font-style: normal;
  }
}
.MuiDialogContent-root .btn-cancel {
  width: 211px;
}
.btn-medium {
  width: 124px;
  height: 55px;
  &.MuiButtonBase-root {
    border-radius: 8px;
    font-weight: 500;
    font-size: 16px;
    color: $nero;
    border: 1px solid $nero;
    font-style: normal;
    text-transform: none;
  }
}
.textInput-small {
  width: 204px;
  height: 55px;
  background-color: $light;
  border-radius: 8px;
  border-style: none;
  text-indent: 10px;
  font-size: 15px;
  color: $dark;
}
.textInput-small:focus {
  outline: none;
}
.textInput-small::placeholder {
  font-size: 15px;
  font-weight: 500;
  color: $gray;
  font-family: $sfProText;
}
.textInput-large {
  width: 422px;
  height: 55px;
  background-color: $light;
  border-radius: 8px;
  border-style: none;
  margin-bottom: 17px;
  text-indent: 10px;
  font-size: 15px;
  color: $dark;
}
.textInput-large:focus {
  outline: none;
}
.textInput-large::placeholder {
  font-size: 15px;
  font-weight: 500;
  color: $gray;
  font-family: $sfProText;
}
.secondary-color {
  color: $secondary;
}
.tertiary-color {
  color: $tertiary;
}
.gray-color {
  color: $gray;
}
.pt1pct {
  padding-top: 1%;
}
.date-text {
  font-weight: 500;
  font-size: 15px;
  font-family: $sfProText;
  font-style: normal;
  color: $dimGray;
}
.header {
  font-weight: 700;
  font-size: 34px;
  color: $dark;
  font-family: $sfProText;
}
.table-container {
  width: 100%;
  &.MuiPaper-rounded {
    border-radius: 16px;
  }
}
.table-row {
  height: 20px;
}
.table-headerBorder {
  border-bottom: 4px solid $gray92;
}
.table-cell {
  &.MuiTableCell-root {
    padding: 0px 16px;
  }
}
.table-head {
  font-weight: 700;
  color: $lightBlack;
  font-size: 12px;
  font-family: $sfProText;
  font-style: normal;
}
.form-title {
  &.MuiTypography-body1 {
    font-family: $sfProText;
    font-size: 12px;
    font-weight: bold;
    line-height: 14.32px;
    height: 14px;
    padding-top: 15px;
    padding-left: 21px;
    padding-bottom: 15px;
  }
  color: $kilimanjaro;
}
.btn-delete {
  width: 211px;
  height: 55px;
  &.MuiButtonBase-root {
    background-color: $tertiary;
    border-radius: 8px;
    font-weight: 500;
    font-size: $fontSize15;
    color: $white;
    font-family: $sfProText;
    font-style: normal;
  }
  &.MuiButtonBase-root:hover {
    background-color: $tertiary;
  }
}
.dialog-content-text {
  &.MuiTypography-body1 {
    font-weight: bold;
    color: $dark;
    font-family: $sfProText;
    font-size: $fontSize18;
    text-align: center;
  }
}
.dialog-container {
  .MuiPaper-rounded {
    border-radius: 16px;
  }
  .MuiDialogContent-root {
    padding: 40px;
  }
  .MuiDialogContent-root:first-child {
    padding-top: 40px;
  }
}
.divider {
  &.MuiDivider-root {
    height: 4px;
    background-color:$gray92,
  }
}
